import { Helmet } from 'react-helmet'
import React, { useState } from 'react';
import { Button, Input, Typography, Space, Divider, Alert, Layout, message } from 'antd';
const { Content } = Layout;
const { Title } = Typography;

const contentStyle: React.CSSProperties = {
  textAlign: 'center',
  minHeight: 24,
  marginTop: 72,
  lineHeight: '0.2rem',
  fontSize: '2rem',
  backgroundColor: '#fff',
};

const info = () => {
  message.info('This is a normal message');
};


const App: React.FC = () => {
  const [inputNumber, setInputNumber] = useState('');
  const [output, setOutput] = useState('');
  const [copyMessage, setCopyMessage] = useState('');

  const symbolMap = {
    90000: 'KZ',
    80000: 'HZ',
    70000: 'NZ',
    60000: 'RZ',
    50000: 'GZ',
    40000: 'EZ',
    30000: 'FZ',
    20000: 'TZ',
    10000: 'CZ',
    9000: 'K',
    8000: 'H',
    7000: 'N',
    6000: 'R',
    5000: 'G',
    4000: 'E',
    3000: 'F',
    2000: 'T',
    1000: 'C',
    900: 'k',
    800: 'h',
    700: 'n',
    600: 'r',
    500: '+',
    400: 'e',
    300: 'f',
    200: 't',
    100: 'c',
    0: 'Z',
  };

  const convertNumberToPattern = (num) => {
    let result = '';
    let strNum = num.toString();
    for (let i = 0; i < strNum.length; i++) {
      let digit = parseInt(strNum[i]);
      let placeValue = Math.pow(10, strNum.length - i - 1);
      let digitValue = digit * placeValue;
      if (digitValue > 0) {
        result += symbolMap[digitValue] || '';
      }
    }
    let pattern = /Z(?=[A-Z])/g;
    // Z
    result = result.replace(pattern, "");
    return result;
  };

  const handleInputChange = (event) => {
    setInputNumber(event.target.value);
  };

  const handleButtonClick = () => {
    const pattern = convertNumberToPattern(parseInt(inputNumber, 10));
    setOutput(pattern);
    copyToClipboard(pattern);
  };

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault();
      handleButtonClick();
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text)
      .then(() => {
        setCopyMessage('');
      })
      .catch((error) => {
        setCopyMessage('');
        console.error(':', error);
      });

    // 
    setTimeout(() => {
      setCopyMessage('');
    }, 3000);
  };

  return (
    <>
      <Helmet>
          <meta charSet="utf-8" />
          <title></title>
      </Helmet>
      <Content style={contentStyle}>
        <Space direction='vertical' style={{ maxWidth: '720px' }} size={[0, 48]}>
          <Title></Title>
            <Space direction="horizontal">
              <Input
                defaultValue=""
                type="number"
                value={inputNumber}
                onChange={handleInputChange}
                onKeyDown={handleKeyDown}
              />
              <Button type="primary" onClick={handleButtonClick}></Button>
            </Space>
            <Alert message="10" type="warning" showIcon />
            <Divider />
            <span style={{ fontWeight: 'bold' }} >{output}</span>
            {copyMessage && <Alert message="" type="success" showIcon />}
          </Space>
        </Content>
    </>
  );
};

export default App;
